.TaskList {
    margin-top: 18px;
    display: flex;
    justify-content: space-between;
}

.TaskList div { display: flex;}

.list-checkbox {
    width: 22px;
    height: 22px;
    margin-right: 12px;
    box-shadow: 0 0 1px #828282;
}

.label-checkbox {
    padding-top: 2px;
    color: #000;
    font-weight: 600;
}

.icon-delete { 
    color: #bdbdbd;
    cursor: pointer;
}
